<template>
  <div>
    <div class="query">
      <el-tabs
        type="card"
        v-model="activeName"
        :stretch="center"
        @tab-click="handleClick">
        <!---->
        <el-tab-pane key="handPane" label="" name="first" :disabled="tabsController">
          <el-form key="hand" label-position="right" label-width="100px" :model="invoiceHand" :ref="invoiceHand" :rules="handRules">
            <el-form-item label="" prop="codes">
              <el-input
                v-if="!firstIn"
                id="codesFirst"
                v-model="invoiceHand.codes"
                v-focus
                v-refocus
                clearable
                maxlength="12"
                minlength="12"
                show-word-limit
                prefix-icon="el-icon-price-tag"
                @blur="cancelFocus"
                placeholder="12"></el-input>
              <el-input
                v-if="firstIn"
                id="codesSecond"
                v-model="invoiceHand.codes"
                v-focus
                clearable
                maxlength="12"
                minlength="12"
                show-word-limit
                prefix-icon="el-icon-price-tag"
                placeholder="12"></el-input>
            </el-form-item>
            <el-form-item label="" prop="numbers">
              <el-input
                v-model="invoiceHand.numbers"
                clearable
                prefix-icon="el-icon-postcard"
                maxlength="8"
                minlength="8"
                show-word-limit
                placeholder="8"></el-input>
            </el-form-item>
            <el-form-item label="" prop="dates">
              <el-date-picker
                v-model="invoiceHand.dates"
                type="date"
                placeholder=""
                style="width:100%"
                clearable
                foramt="yyyyMMdd"
                value-format="yyyyMMdd">
              </el-date-picker>
            </el-form-item>
            <el-form-item label="6" prop="checkCodes">
              <el-input
                v-model="invoiceHand.checkCodes"
                clearable
                maxlength="6"
                minlength="6"
                show-word-limit
                prefix-icon="el-icon-document-checked"
                placeholder="6"></el-input>
            </el-form-item>
          </el-form>
          <el-row>
            <p></p>
            <el-button
              type="primary"
              round
              v-on:click="listener"
              icon="el-icon-view"
              :loading="isLoading1"></el-button>
            <el-button
              type="success"
              round
              :loading="firstQuery"
              icon="el-icon-view"
              @click="showResult = true"></el-button>
          </el-row>
        </el-tab-pane>
        <!---->
        <el-tab-pane key="scannerPane" label="" name="second" :disabled="tabsController">

          <el-form key="scanner" label-position="right" label-width="100px" :model="scanner" :ref="scanner" :rules="scannerRules">
            <h5 align="right">
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-link type="primary" icon="el-icon-question" @click="drawerHelp1=true">, </el-link>
              </el-tooltip>
            </h5>
            <el-form-item label="" prop="code">
              <el-input
                v-model="scanner.code"
                placeholder=""
                clearable
                v-focus
                v-refocus
                prefix-icon="el-icon-price-tag"
                @blur="focusReminder"
                @change="scannerInputting"
                @clear="clearScanner">
              </el-input>
            </el-form-item>
            <el-form-item label="" prop="number">
              <el-input
                v-model="scanner.number"
                clearable
                placeholder=""
                prefix-icon="el-icon-postcard"
                @clear="clearScanner"></el-input>
            </el-form-item>
            <el-form-item label="" prop="dates">
              <el-input
                v-model="scanner.dates"
                placeholder="yyyyMMdd"
                clearable
                prefix-icon="el-icon-date"
                @clear="clearScanner">
              </el-input>
            </el-form-item>
            <el-form-item label="6" prop="checkCode">
              <el-input
                v-model="scanner.checkCode"
                placeholder="6"
                clearable
                prefix-icon="el-icon-document-checked"
                @clear="clearScanner"></el-input>
            </el-form-item>
          </el-form>
          <el-row>
            <p></p>
            <el-button
              type="primary"
              round
              v-on:click="scannerListener"
              :loading="isLoading2"
              icon="el-icon-search" ></el-button>
            <el-button
              type="success"
              round
              :loading="secondQuery"
              @click="showResult = true"
              icon="el-icon-view"></el-button>
          </el-row>
        </el-tab-pane>
        <!---->
        <el-tab-pane label="" name="third" :disabled="tabsController">
          <el-upload
            class="upload-demo"
            ref="upload"
            action="http://localhost:8090/query/picture"
            :data="uploadPara"
            name="userHeaderPicture"
            :file-list="pictures"
            :on-preview="handlePreview"
            :on-remove="handleRemove"
            :on-success="handleAvatarSuccess"
            :on-exceed="handleExceed"
            :on-change="handleChange"
            multiple
            :limit="3"
            list-type="picture"
            :auto-upload="false"
            accept=".png, .jpg">
            <el-button
              slot="trigger"
              size="medium"
              type="primary"
              @click="pictures=[], picNumber=0"
              round
              icon="el-icon-folder-add"
              :loading="isLoading3"></el-button>
            <el-button
              style="margin-left: 10px;"
              size="medium" type="success"
              @click="submitUpload"
              round
              icon="el-icon-upload"
              :loading="isLoading3"></el-button>
            <el-button
              style="margin-left: 10px;"
              size="medium"
              type="info" @click="pictures=[], picNumber=0"
              round
              icon="el-icon-folder-delete"
              :loading="isLoading3"></el-button>
            <h5 align="center">
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-link type="primary" icon="el-icon-question" @click="drawerHelp2=true">jpg/png, 500kb, 3</el-link>
              </el-tooltip>
            </h5>
            <el-button style="margin-left: 10px;" size="medium" type="primary" round :loading="thirdQuery" @click="showResult = true"></el-button>
            <el-button style="margin-left: 10px;" size="medium" type="primary" round v-on:click="testing"></el-button>
          </el-upload>
        </el-tab-pane>
      </el-tabs>

      <!---->
      <el-drawer
        id="helpScanner"
        :visible.sync="drawerHelp1"
        :with-header="false">
        <h3></h3>
        <h4 align="left">&nbsp&nbsp1. </h4>
        <p align="left">
          &nbsp&nbsp&nbsp&nbsp&nbsp(1) USBUSB
        </p>
        <p align="left">
          &nbsp&nbsp&nbsp&nbsp&nbsp(2) 
        </p>
        <p></p>
        <h4 align="left">&nbsp&nbsp2. </h4>
        <p align="left">
          &nbsp&nbsp&nbsp&nbsp&nbsp(1) , 
        </p>
        <el-image
          :src="require('../assets/help_1.png')"
          lazy
          draggable="false">
        </el-image>
        <p align="left">
          &nbsp&nbsp&nbsp&nbsp&nbsp(2) 
        </p>
        <p align="left">
          &nbsp&nbsp&nbsp&nbsp&nbsp(3) 
        </p>
        <el-image
          :src="require('../assets/help_2.png')"
          lazy
          draggable="false">
        </el-image>
        <p align="left">
          &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp :
        </p>
        <p align="left">
          &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp&nbsp 1286
        </p>
        <p align="left">
          &nbsp&nbsp&nbsp&nbsp&nbsp(4) 
        </p>
        <p align="left">
          &nbsp&nbsp&nbsp&nbsp&nbsp(5) 
        </p>
      </el-drawer>
      <!---->
      <el-drawer
        id="localImageScanner"
        :visible.sync="drawerHelp2"
        :with-header="false">
        <h3></h3>
        <h4 align="left">&nbsp&nbsp1. </h4>
        <p></p>
        <el-image
          :src="require('../assets/help_img_1.png')"
          lazy
          draggable="false">
        </el-image>
        <h4 align="left">&nbsp&nbsp2. </h4>
        <el-image
          :src="require('../assets/help_img_2.png')"
          lazy
          draggable="false">
        </el-image>
        <h4 align="left">&nbsp&nbsp3. </h4>
        <el-image
          :src="require('../assets/help_img_3.png')"
          lazy
          draggable="false">
        </el-image>
      </el-drawer>
      <!---->

      <el-dialog
        :visible.sync="showResult"
        center
        fullscreen>
        <h1 v-html="msg_result" align="center"></h1>
        <div class="dialog">
          <el-image
            v-if="showPicture"
            :src="paths"
            lazy
            :preview-src-list="imgLists">
          </el-image>
        </div>
        <div slot="footer" class="dialog-footer">
          <p>
            
          </p>
          <el-button type="primary" @click="showResult = false"></el-button>
        </div>
      </el-dialog>
    </div>
    <div class="footer">
      2020 Jin Cai Technology 
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default
  {
    name: 'Query',
    data() {
      return {
        //
        pictures: [],
        center: true,
        //tab
        tabsController: false,
        //
        invoiceHand: {
          codes: '',
          numbers: '',
          dates: '',
          checkCodes: ''
        },
        //
        scanner: {
          code: '',
          number: '',
          dates: '',
          checkCode: ''
        },
        //
        handRules: {
          codes: [
            {
              required: true,
              message: "",
              trigger: "blur"
            }
          ],
          numbers: [
            {
              required: true,
              message: "",
              trigger: "blur"
            }
          ],
          dates: [
            {
              required: true,
              message: "",
              trigger: "blur"
            }
          ],
          checkCodes: [
            {
              required: true,
              message: "6",
              trigger: "blur"
            }
          ]
        },
        //
        scannerRules: {
          code: [
            {
              required: true,
              message: "",
              trigger: "blur"
            }
          ],
          number: [
            {
              required: true,
              message: "",
              trigger: "blur"
            }
          ],
          dates: [
            {
              required: true,
              message: "",
              trigger: "blur"
            }
          ],
          checkCode: [
            {
              required: true,
              message: "6",
              trigger: "blur"
            }
          ]
        },
        //
        firstIn: false,
        //
        firstQuery: true,
        secondQuery: true,
        thirdQuery: true,
        isLoading1: false,
        isLoading2: false,
        isLoading3: false,
        //
        activeName: 'first',
        //
        drawerHelp1: false,
        drawerHelp2: false,
        //loading
        picIsLoading: true,
        //
        picNumber: 0,
        picCount: 0,
        user_id: '',
        //id
        uploadPara: {},
        //
        queryType: 1,
        //
        queryResult: {},
        //
        showResult: false,
        //
        msg_result: '',
        //
        showPicture: false,
        //
        paths: '',
        //
        imgLists:[]
      };
    },
    methods: {
      testing()
      {
        console.log(this.picNumber + " Testing...")
      },
      /*
      
      
      */
      handleClick(tab, event)
      {
        if(!this.isLoading1 && !this.isLoading2 && !this.isLoading3)
        {
          console.log(this.picNumber);
          this.activeName = tab.name;
          this.queryResult = {};
          this.firstQuery = true;
          this.secondQuery = true;
          this.thirdQuery = true;

          if(this.activeName == 'first')
          {
            this.invoiceHand.codes = '';
            this.invoiceHand.numbers = '';
            this.invoiceHand.dates = '';
            this.invoiceHand.checkCodes = '';
          }
          else if(this.activeName == 'second')
          {
            this.scanner.code = '';
            this.scanner.number = '';
            this.scanner.dates = '';
            this.scanner.checkCode = '';
            this.firstIn = false;
          }
          else if(this.activeName == 'third')
          {
            this.pictures = [];
            this.picNumber = 0;
            this.picIsLoading = true;
          }
        }
      },
      /*
      
      */
      cancelFocus()
      {
        this.firstIn = true;
      },
      /*
      
      
      */
      listener()
      {
        this.queryType = 1;
        this.queryResult = {};
        this.firstQuery = true;
        this.paths = '';
        this.imgLists = [];
        this.msg_result = '';
        let isCodes = false;
        let isNumbers = false;
        let isDates = false;
        let isChecks = false;
        if(this.invoiceHand.codes != '')
          isCodes = true;
        if(this.invoiceHand.numbers != '')
          isNumbers = true;
        if(this.invoiceHand.dates != '')
          isDates = true;
        if(this.invoiceHand.checkCodes != '')
          isChecks = true;
        if(!isCodes)
          this.$message.error('');
        else if(!isNumbers)
          this.$message.error('');
        else if(!isDates)
          this.$message.error('');
        else if(!isChecks)
          this.$message.error('');
        if(isCodes && isNumbers && isChecks && isDates)
        {
          this.isLoading1 = true;
          this.tabsController = true;
          let reqStr = 'http://localhost:8090/query/normal' + '?' + 'userId=' + this.user_id + '&' +
            'invoice_code=' + this.invoiceHand.codes + '&invoice_number=' +
            this.invoiceHand.numbers + '&invoice_date=' + this.invoiceHand.dates +
            '&invoice_check=' + this.invoiceHand.checkCodes;
          axios.get(reqStr).then(response =>
          {
            this.queryResult = response.data
            if(this.queryResult.codes == "0")
            {
              this.$message.success('');
              this.msg_result = "";
              let arrays = this.queryResult.waterMarkPath.toString().split("\\");
              let name = arrays[arrays.length - 1];
              //
              this.paths = "http://localhost:8090/query/images?type=watermark&name=" + name;
              this.imgLists.push(this.paths);
              this.showPicture = true;
            }
            else if(this.queryResult.codes == "1")
            {
              this.$message.warning('');
              this.msg_result = "" + "<br/>" + ": " + this.queryResult.codes +
                "<br/>" + ": " + this.queryResult.comments;
              let arrays = this.queryResult.waterMarkPath.toString().split("\\");
              let name = arrays[arrays.length - 1];
              console.log(name);
              this.paths = "http://localhost:8090/query/images?type=watermark&name=" + name;
              this.imgLists.push(this.paths);
              this.showPicture = true;
            }
            else
            {
              this.$message.error('');
              this.msg_result = "" + "<br/>: " + this.queryResult.comments
              + "<br/> ";
              this.showPicture = true;
            }
            this.firstQuery = false;
            this.isLoading1 = false;
            this.tabsController = false;
            //console.log(response)
          });
        }
      },
      /*
       
       */
      focusReminder()
      {
        if(this.activeName == 'second' && this.scanner.code == '')
        {
          this.$message.error('');
        }
      },
      /*
      
      */
      scannerInputting()
      {
        if(this.scanner.code.split(",").length > 1)
        {
          let splitInput = this.scanner.code.split(",")
          this.scanner.code = splitInput[2];
          this.scanner.number = splitInput[3];
          this.scanner.dates = splitInput[5];
          this.scanner.checkCode = splitInput[6].substring(splitInput[6].length - 6, splitInput[6].length);
        }
      },
      /*
      
      */
      clearScanner()
      {
        this.scanner.code = '';
        this.scanner.number = '';
        this.scanner.dates = '';
        this.scanner.checkCode = '';
      },
      /*
       
       */
      scannerListener()
      {
        console.log("Scanner")
        this.queryType = 2;
        this.queryResult = {};
        this.secondQuery = true;
        this.paths = '';
        this.imgLists = [];
        this.msg_result = '';
        this.tabsController = true;
        this.$refs[this.scanner].validate(value =>
        {
          if(value)
          {
            this.isLoading2 = true;
            let reqStr = 'http://localhost:8090/query/scanner' + '?' + 'userId=' + this.user_id + '&' +
              'invoice_code=' + this.scanner.code + '&invoice_number=' +
              this.scanner.number + '&invoice_date=' + this.scanner.dates +
              '&invoice_check=' + this.scanner.checkCode;
            axios.get(reqStr).then(response =>
            {
              this.queryResult = response.data
              console.log(response);
              if(this.queryResult.codes == "0")
              {
                this.$message.success('');
                this.msg_result = "";
                let arrays = this.queryResult.waterMarkPath.toString().split("\\");
                let name = arrays[arrays.length - 1];
                console.log(name);
                this.paths = "http://localhost:8090/query/images?type=watermark&name=" + name;
                this.imgLists.push(this.paths);
                this.showPicture = true;
              }
              else if(this.queryResult.codes == "1")
              {
                this.$message.warning('');
                this.msg_result = "" + "<br/>" + ": " + this.queryResult.codes +
                "<br/>" + ": " + this.queryResult.comments;
                let arrays = this.queryResult.waterMarkPath.toString().split("\\");
                let name = arrays[arrays.length - 1];
                console.log(name);
                this.paths = "http://localhost:8090/query/images?type=watermark&name=" + name;
                this.imgLists.push(this.paths);
                this.showPicture = true;
              }
              else
              {
                this.$message.error('');
                this.msg_result = "" + "<br/>: " + this.queryResult.comments +
                "<br/>";
                this.showPicture = true;
              }
              this.secondQuery = false;
              this.isLoading2 = false;
              this.tabsController = false;
            });
          }
        });
      },
      submitUpload()
      {
        this.queryType = 3;
        console.log(this.picNumber + " AAAAA")
        if(!this.isLoading3 && this.picNumber >= 1)
        {
          this.queryResult = {};
          //
          this.paths = '';
          this.imgLists = [];
          this.msg_result = '';
          this.picCount = 0;
          //loading
          this.isLoading3 = true;
          this.thirdQuery = true;
          this.$refs.upload.submit();
        }
      },
      /*
      
      */
      handleExceed(files, fileList) {
        this.$message.warning(` 3  ${files.length}  ${files.length + fileList.length} `);
      },
      handleRemove(file, fileList)
      {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      //
      handleAvatarSuccess(res, files)
      {
        this.$message.success('');
        this.queryResult = {};
        this.thirdQuery = true;
        this.paths = '';
        this.imageUrl = URL.createObjectURL(files.raw);
        let codeInfo = res.codes;

        if(codeInfo == 0)
        {
          this.$message.success('');
        }
        else if(codeInfo == 1)
        {
          this.$message.warning('');
          this.msg_result = "" + "<br/>" + ": " + this.queryResult.codes +
            "<br/>" + ": " + this.queryResult.comments;
          let arrays = this.queryResult.waterMarkPath.toString().split("\\");
          let name = arrays[arrays.length - 1];
          this.paths = "http://localhost:8090/query/images?type=watermark&name=" + name;
          this.imgLists.push(this.paths);
        }
        else
        {
          this.$message.error('');
          this.msg_result = "" + "<br/>: " + this.queryResult.comments;
        }

        console.log(res.codes + " Hello!");
        this.picCount = this.picCount + 1;
        console.log("Successful!" + this.picCount)
        if(this.picNumber == this.picCount)
        {
          console.log("")
          this.isLoading3 = false;
          this.tabsController = false;
          //
          this.thirdQuery = false;
        }
        //this.ruleForm.userHeaderPicture = res.data;
      },
      //
      handleChange(file, fileList)
      {
        this.picNumber = fileList.length;
      },
    },
    directives: {
      focus: {
        inserted: function(el) {
          el.querySelector("input").focus();
        }
      },
      refocus: {
        update: function(el)
        {
          el.querySelector("input").focus();
        }
      }
    },
    created ()
    {
      let id = this.$loginInfo.usersId.toString();
      this.user_id = id;
      this.uploadPara = {"user": id}
    }
  }
</script>

<style scoped>
  .query
  {
    border-radius: 15px;
    background-clip: padding-box;
    margin: 80px auto;
    width: 600px;
    height: 500px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #fff;
    box-shadow: 0 0 25px #fff;
  }
  .dialog
  {
    text-align: center;
    border-radius: 15px;
    background-clip: padding-box;
    margin: 0px auto;
    width: 500px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #fff;
  }
  .footer
  {
    position: fixed;
    margin: auto;
    bottom: 10px;
    width: 100%;
  }
</style>



