<template>
  <div class="EchartPractice">
    <div id="main"></div>
  </div>
</template>

<script>
export default {
  name: "EchartPractice",
  methods:{
    drawChart() {
      let myEchart = this.$echarts.init(document.getElementById("main"));
      let option = {
        title:{
          text:"VueEcharts"
        },
        tooltip:{},
        legend:{
          data:[""]
        },
        xAxis:{
          data: ["","","","",]
        },
        yAxis:{},
        series:[{
          name:'',
          type:"bar",
          data:[50,60,45,30,]
        }]
      };
      myEchart.setOption(option);
    }
  },
  mounted() {
    this.drawChart();
  }
}
</script>

<style scoped>
#main {
  width: 600px;
  height:400px;
  margin: auto;
  margin-top: 100px
}
</style>




